* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;

}

body {
  background-image: linear-gradient(90deg, #00132B 0%, #1B1F24 98.96%);
  color: var(--neutral--400);
  font-family: Spacegrotesk, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.667em;
}

.header-wrapper {
  padding-top: 36px;
  padding-bottom: 36px;
}


.bg-secundario {
  background-color: var(--neutral--700);
}

.text-200 {
  line-height: 1.111em;
}

.text-200.bold {
  color: var(--neutral--100);
  font-weight: 700;
}

.text-uppercase {
  letter-spacing: .1em;
  text-transform: uppercase;
}

.cor-1 {
  color: var(--neutral--100);
}

